import React, { useEffect, useState } from "react";
import { collection, getDocs } from "firebase/firestore";
import { db } from "./firebase";
import { Bar, Pie } from "react-chartjs-2";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  ArcElement,
  Tooltip,
  Legend,
} from "chart.js";

ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  ArcElement,
  Tooltip,
  Legend
);

function ExpenseList() {
  const [expenses, setExpenses] = useState([]);

  useEffect(() => {
    const fetchExpenses = async () => {
      const snapshot = await getDocs(collection(db, "expenses"));
      const items = snapshot.docs.map((doc) => ({ id: doc.id, ...doc.data() }));
      setExpenses(items);
    };

    fetchExpenses();
  }, []);

  const totalByPerson = expenses.reduce(
    (acc, e) => {
      if (e.paidBy === "Nishant") acc.nishant += e.amount;
      else if (e.paidBy === "Rajat") acc.rajat += e.amount;
      return acc;
    },
    { nishant: 0, rajat: 0 }
  );

  const categories = ["Groceries", "Meat", "Outside Food", "Misc"];
  const totalByCategory = categories.map(
    (cat) =>
      expenses
        .filter((e) => e.category === cat)
        .reduce((sum, e) => sum + e.amount, 0)
  );

  const pieData = {
    labels: ["Nishant", "Rajat"],
    datasets: [
      {
        label: "Contribution",
        data: [totalByPerson.nishant, totalByPerson.rajat],
        backgroundColor: ["#4f46e5", "#e11d48"],
        hoverOffset: 10,
      },
    ],
  };

  const barData = {
    labels: categories,
    datasets: [
      {
        label: "Expenses by Category",
        data: totalByCategory,
        backgroundColor: "#4f46e5",
      },
    ],
  };

  return (
    <div style={{ maxWidth: "900px", margin: "40px auto", fontFamily: "Poppins, sans-serif" }}>
      <h2 style={{ textAlign: "center" }}>Expenses</h2>

      <table
        style={{
          width: "100%",
          borderCollapse: "collapse",
          marginBottom: "20px",
          boxShadow: "0 0 10px rgb(0 0 0 / 0.1)",
        }}
      >
        <thead style={{ backgroundColor: "#4f46e5", color: "white" }}>
          <tr>
            <th style={th}>Description</th>
            <th style={th}>Amount (₹)</th>
            <th style={th}>Paid By</th>
            <th style={th}>Category</th>
          </tr>
        </thead>
        <tbody>
          {expenses.map(({ id, description, amount, paidBy, category }) => (
            <tr key={id} style={{ textAlign: "center", borderBottom: "1px solid #ddd" }}>
              <td style={td}>{description}</td>
              <td style={td}>{amount}</td>
              <td style={td}>{paidBy}</td>
              <td style={td}>{category}</td>
            </tr>
          ))}
        </tbody>
        <tfoot>
          <tr style={{ fontWeight: "bold", backgroundColor: "#e0e7ff" }}>
            <td>Total</td>
            <td>{(totalByPerson.nishant + totalByPerson.rajat).toFixed(2)}</td>
            <td>
              Nishant: ₹{totalByPerson.nishant.toFixed(2)} <br />
              Rajat: ₹{totalByPerson.rajat.toFixed(2)}
            </td>
            <td></td>
          </tr>
        </tfoot>
      </table>

      <div
        style={{
          display: "flex",
          justifyContent: "space-around",
          gap: "30px",
          flexWrap: "wrap",
        }}
      >
        <div style={{ width: "400px" }}>
          <Bar data={barData} />
        </div>

        <div style={{ width: "400px" }}>
          <Pie data={pieData} />
        </div>
      </div>
    </div>
  );
}

const th = {
  padding: "12px",
  border: "1px solid #ddd",
};

const td = {
  padding: "12px",
  border: "1px solid #ddd",
};

export default ExpenseList;
